import IconMenu from '@/components/IconMenu/iconmenu.vue'
export default {
    data(){
        return {
            test:'',
            ScrollHead:false,
            hdChangeBg:'rgba(60, 185, 99, 0)',
            CHZXM:'',   // 患者姓名
            CSFZH:'',   // 患者身份证
            otherObj:'',
            isOther: false,
            menulist:[
                {
                    url:'Information',
                    imgSrc:require('../imgs/HealthRecords/basicInfo.png'),
                    text:'基本信息',
                },
                {
                    url:'medicineIndex',
                    imgSrc:require('../imgs/HealthRecords/medicineRecord.png'),
                    text:'就诊记录',
                },
                {
                    url:'PhysicalRecords',
                    imgSrc:require('../imgs/HealthRecords/physicalRecord.png'),
                    text:'体检记录',
                },
                {
                    url:'WomenHealth',
                    imgSrc:require('../imgs/HealthRecords/woman.png'),
                    text:'妇女保健',
                },
                {
                    url:'ChildHealth',
                    imgSrc:require('../imgs/HealthRecords/children.png'),
                    text:'儿童保健',
                },
                {
                    url:'maternalHealth',
                    imgSrc:require('../imgs/HealthRecords/gravida.png'),
                    text:'孕产妇保健',
                },
                {
                    url:'chronicDisease',
                    imgSrc:require('../imgs/HealthRecords/chronic.png'),
                    text:'慢性病信息',
                },
                {
                    url:'SpecialDisease',
                    imgSrc:require('../imgs/HealthRecords/special.png'),
                    text:'专病信息',
                },
                {
                    url:'TCMoldManagement',
                    imgSrc:require('../imgs/HealthRecords/traditional.png'),
                    text:'中医药管理',
                },
                {
                    url:'otherMedicineServer',
                    imgSrc:require('../imgs/HealthRecords/otherServer.png'),
                    text:'其他医疗服务',
                },
            ]
        }
    },
    mounted() {
        this.test= JSON.parse(window.localStorage.getItem('login'));
        if(window.localStorage.getItem('other')){  // 档案其他人的档案
            this.otherObj =  JSON.parse(window.localStorage.getItem('other'));
            this.isOther = true;
            this.$root.loginInfo.CHZXM = this.otherObj.JMXM;    // 姓名
            this.$root.loginInfo.CSFZH = this.otherObj.ZJBH;    // 身份证号
            this.$root.loginInfo.jkdabh = this.otherObj.JKDABH;    // 健康档案编码
            this.$root.loginInfo.CHZTX = this.otherObj.CHZTX;    // 头像
        }else{
            // this.$root.loginInfo.CHZXM = this.test.CHZXM;    // 姓名
            // this.$root.loginInfo.CSFZH = this.test.CSFZH;    // 身份证号
            // this.$root.loginInfo.jkdabh = this.test.CJKDABH;   // 健康档案编码
            // this.$root.loginInfo.CHZTX = this.$root.hostImage+this.test.CHZTX;    // 头像
        }
        this.setHeaderBg();
    },
    methods:{
        getSFZHM(SFZHM){
            if( typeof SFZHM=='undefined'||SFZHM ==''){
                return 'xx';
            }else{
                return SFZHM.substring(0,10)+'xxxxxxx'+SFZHM.substring(16,18);
            }

        },
        goBackHome() {
            if(window.localStorage.getItem('other')){
                this.test= JSON.parse(window.localStorage.getItem('login'));
                this.$root.loginInfo.CHZXM = this.test.CHZXM;    // 姓名
                this.$root.loginInfo.CSFZH = this.test.CSFZH;    // 身份证号
                this.$root.loginInfo.jkdabh = this.test.CJKDABH;   // 健康档案编码
                this.$root.loginInfo.CHZTX = this.$root.hostImage+this.test.CHZTX;    // 头像
                window.localStorage.removeItem('other');
                this.$router.goBack();
            }else{
                this.$router.isBack = true;
                this.$router.push('/home');
            }
            
        },
        setHeaderBg(){
            window.addEventListener('scroll',(event)=>{
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                if(scrollTop<=0){
                    this.ScrollHead = false;
                }else{
                    this.ScrollHead = true;
                    
                }
                let opt = scrollTop/150>0.9?0.9:scrollTop/150;
                this.hdChangeBg = `rgba(60, 185, 99,${opt})`;             
            })
        }
    },
    components:{
        IconMenu
    }
}



